.tui-skeleton {
  position: relative;
  color: transparent !important;
  background: transparent !important;
  pointer-events: none;
  user-select: none;
}
.tui-skeleton:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background-color: rgba(0, 0, 0, 0.08);
  animation: tuiSkeletonBackgroundVibe ease-in-out 1s infinite alternate;
}
.tui-skeleton_light:after {
  background-color: rgba(0, 0, 0, 0.16);
  animation-name: tuiSkeletonLightBackgroundVibe;
}
.tui-skeleton_rounded:after {
  border-radius: inherit;
}
.tui-skeleton_short:after {
  width: 40%;
}
.tui-skeleton_center:after {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  width: 40%;
}
.tui-skeleton_text:before {
  content: '\a0';
}
.tui-skeleton * {
  opacity: 0;
  visibility: hidden;
}
